/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.cta1 {
  color: #fff;
  background-color: #00BFC6;
  padding: 1rem 3.8rem;
  border-radius: 0.5rem;
  font-weight: 700;
  transition-property: background-color ease-in;
  transition-duration: 0.3s;
}
.cta1:hover {
  background-color: #01adb3;
}

.cta2 {
  background-color: #fff;
  padding: 1rem 3.8rem;
  font-weight: 700;
  border: 2px #B7B7B7 solid;
  border-radius: 0.5rem;
  transition-property: background-color ease-in;
  transition-duration: 0.3s;
}
.cta2:hover {
  background-color: #EDEDED;
}

.card {
  padding: 3rem 1.5rem;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  cursor: pointer;
}
@media (min-width: 992px) {
  .card {
    transition-property: transform ease-in;
    transition-duration: 0.3s;
    transform: scale(1);
  }
  .card:hover {
    transform: scale(1.05);
  }
}

